const app = getApp();
import { mapMutations } from "vuex";

export default {
    data() {
        return {
            mobile: "",
            vcode: "",
            logining: false
        };
    },
    onLoad() {},
    methods: {
        ...mapMutations(["login"]),
        inputChange(e) {
            const key = e.currentTarget.dataset.key;
            this[key] = e.detail.value;
        },
        async getVcode() {
            if (!/^1[3456789]\d{9}$/.test(this.mobile)) {
                this.$api.msg("请输入正确的手机号码");
                return;
            }
            const res = await app.req("special/login/sendMobileCode", {
                mobile: this.mobile
            });

            if (res.data.code == 0) {
                this.$api.msg("发送成功");
            } else {
                this.$api.msg(res.data.msg);
            }
        },
        navBack() {
            uni.navigateBack();
        },
        toRegist() {
            this.$api.msg("去注册");
        },
        async toLogin() {
            this.logining = true;
            const { mobile, vcode } = this;
            /* 数据验证模块
				if(!this.$api.match({
					mobile,
					password
				})){
					this.logining = false;
					return;
				}
				*/
            const sendData = {
                mobile,
                code: vcode
            };

            const res = await app.req("special/login/login", sendData);
            if (res.data.code == 0) {
				uni.setStorageSync("session_key", res.data.auth);
                this.$api.msg('登陆成功');
                uni.$emit('login_hide')
				setTimeout(() => {
                	uni.navigateBack();
				}, 1000);
            } else {
                this.$api.msg(res.data.msg);
            }
            // if (result.status === 1) {
            //     this.login(result.data);
            //     uni.navigateBack();
            // } else {
            //     this.$api.msg(result.msg);
            //     this.logining = false;
            // }
        }
    }
};
